.catalogItem {
    width: 326px;
    height: 490px;
    background: #ffffff;
    box-shadow: 0px 14px 39px -11px rgba(0, 0, 0, 0.06);
    // margin-right: 21px;
    margin-bottom: 10px;
    border-radius: 10px;
    @media (max-width: 630px) {
        width: 290px;
        height: 490px;
    }

    .catalogItem__box {
        padding: 29px 23px;

        .product__photo {
            text-align: center;
            img {
                max-width: 290px;
                max-height: 220px;
            }
        }
        .product__size {
            display: flex;
            p {
                margin: 0;
                font-weight: 400;
                font-size: 12px;
                line-height: 130%;
                color: #3f4e65;
            }
        }
        .product__name {
            font-weight: 700;
            font-size: 16px;
            line-height: 130%;
            color: #111111;
        }
        .product__addition {
            margin: 20xp 0 15px;
            .addition__title {
                font-weight: 300;
                font-size: 14px;
                line-height: 150%;
                color: #3f4e65;
                p {
                    margin: 0;
                    margin-left: 5px;
                    font-weight: 600;
                    font-size: 14px;
                    line-height: 150%;
                    color: #111111;
                }
                display: flex;
            }
        }
    }
    @media (max-width: 400px) {
        .catalogItem__box {
            padding: 29px 15px;
            .product__photo {
                img {
                    max-width: 275px;
                }
            }
        }
    }
}

.catalogItem__box-toCard {
    display: flex;
    align-items: center;
    font-family: "Inter";
    font-style: normal;
    font-weight: 800;
    font-size: 16px;
    line-height: 130%;
    color: #111111;
    margin-top: 15px;
}
.addToCard-btn {
    margin-left: 40px;
    width: 153px;
    height: 45px;
    cursor: pointer;
    border: none;
    outline: none;
    background: #ffc85e;
    border-radius: 80px;
    color: #ffffff;
}
