.modal {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    width: 100%;
    z-index: 9999;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: rgba(0, 0, 0, 0.25);
    animation-name: appear;
    animation-duration: 300ms;
}

.modal-dialog {
    width: 100%;
    max-width: 735px;
    height: 700px;
    position: relative;
    margin: 0 20px;
    max-height: calc(100vh - 40px);
    text-align: left;
    display: flex;
    flex-direction: column;
    overflow: hidden;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    -webkit-animation-name: animatetop;
    -webkit-animation-duration: 0.4s;
    animation-name: slide-in;
    animation-duration: 0.5s;
    background: #ffffff;
    border-radius: 8px;
}

.modal-header,
.modal-footer {
    display: flex;
    align-items: center;
}
.modal-header {
    justify-content: space-between;
}
.modal-footer {
    border-top: 1px solid #dbdbdb;
    justify-content: flex-end;
}
.modal-close {
    cursor: pointer;
    font-size: 32px;
    // padding: 1rem;
    margin: 36px 36px -1rem auto;
    color: #ffc85e;
}
.modal-body {
    overflow: auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    align-items: flex-start;
    margin-left: 10px;
    margin-top: 56px;
    .item {
        display: flex;
        align-items: center;
        input {
            margin-left: 10px;
            height: 20px;
        }
        select {
            margin-left: 10px;
            height: 20px;
        }
    }
}
.modal-content {
    padding: 1rem;
}

.modal-title {
    margin: 0;
}

@keyframes appear {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}
@keyframes slide-in {
    from {
        transform: translateY(-150px);
    }
    to {
        transform: translateY(0);
    }
}

.modal__title {
    text-align: center;
}
.modal__save {
    display: flex;
    flex-direction: column;
    align-items: center;
}
.modal__save-btn {
    cursor: pointer;
    border: none;
    outline: none;
    margin-top: 20px;
    background: #ffc85e;
    border-radius: 80px;
    color: #ffffff;
    height: 40px;
    width: 130px;
    margin-bottom: 20px;
}
.modal__save-error {
    font-size: 16px;
    color: red;
}
.modal__save-added {
    display: block;
    font-size: 16px;
    color: green;
}
.modal__save-notadded {
    display: none;
}
